import { formCheck } from '@/config/checkout'
import { ALERT_TIME } from '@/config/config'
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: "login",
  data () {
    return {
      mobile: '', // æ‰‹æœºå·
      pwd: '', // å¯†ç 
      verify_code: '', //éªŒè¯ç 
      img_code: '', // å›¾å½¢éªŒè¯ç 
    };
  },
  computed: {
    ...mapGetters([
      'haveLogin',
      'userInfo'
    ])
  },
  created () {
    this.get_img_code()
  },
  methods: {
    // èŽ·å–å›¾å½¢éªŒè¯ç 
    get_img_code () {
      this.$Api({
        api_name: 'kkl.user.getImgCode',
      }, (err, data) => {
        if (!err) {
          this.img_code = data.data.url
        } else {
          this.$msg(err.error_msg, 'error', 1500)
        }
      })
    },
    // æ›´æ¢å›¾å½¢éªŒè¯ç 
    change_code_img () {
      this.img_code = ''
      this.get_img_code()
    },
    // æ³¨å†Œè·³è½¬
    register () {
      this.$router.push({
        path: '/register'
      })
    },
    // å¿˜è®°å¯†ç è·³è½¬
    getPwd () {
      this.$router.push({
        path: '/forgetPwd'
      })
    },
    // ç™»å½•
    login () {
      let checkItem = [{
        reg: 'mobile',
        val: this.mobile,
        errMsg: 'è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·'
      }, {
        reg: 'noData',
        val: this.pwd,
        errMsg: 'å¯†ç ä¸èƒ½ä¸ºç©º'
      }, {
        reg: 'noData',
        val: this.verify_code,
        errMsg: 'æ ¡éªŒç ä¸èƒ½ä¸ºç©º'
      }]
      if (formCheck(checkItem).result) {
        this.$Api({
          api_name: 'kkl.user.login',
          mobile: this.mobile,
          password: this.$MD5.hex_md5(this.pwd),
          code: this.verify_code
        }, (err, data) => {
          if (!err) {
            this.$toast({ text: 'ç™»å½•æˆåŠŸ' });
            this.$router.replace({
              path: '/'
            })
            localStorage.setItem('loginStatu',1)
            this.$Api({ api_name: 'kkl.user.getUserInfo' }, (erra, res) => {
              if (!erra) {
                this.setUser(res.data)
              } else {
                this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
              }
            })
          } else {
            if (data.code === 40051) {
              this.img_code = ''
              this.get_img_code()
            }
            this.$msg(err.error_msg, 'cancel', 'middle', 1500)
          }
        })
      } else {
        this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
      }
    },
    ...mapMutations({
      setUser: 'SET_USER',
      delUser: 'DEL_USER'
    })
  }
}



