import MescrollVue from 'mescroll.js/mescroll.vue'
export default {
    components:{
        MescrollVue
    },
    name: 'rankReward',
    data () {
        return {
            list: [{
                title: 'é¢†å¥–æ—¶é—´',
            },{
                title: 'é¢†å¥–é‡‘é¢',
            }],
            relief_list: [],
            mescroll: null, // mescrollå®žä¾‹å¯¹è±¡
            mescrollDown:{
                use: false
            }, 
            mescrollUp: { // ä¸Šæ‹‰åŠ è½½çš„é…ç½®.
                callback: this.upCallback, // ä¸Šæ‹‰å›žè°ƒ,
                page: {
                    num: 0, 
                    size: 10
                },
                htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
            },
            reward:'',
            is_receive: ''
        }
    },
    created() {
        this.get_reward()
    },
    methods: {
        // èŽ·å–æŽ’è¡Œæ¦œå¥–åŠ±
        get_reward() {
            this.$Api({
                api_name: 'kkl.user.getRankReward',
            }, (err, data) => {
                if (!err) {
                    this.reward = data.data.reward
                    this.is_receive = data.data.is_receive
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        mescrollInit (mescroll) {
            this.mescroll = mescroll  // å¦‚æžœthis.mescrollå¯¹è±¡æ²¡æœ‰ä½¿ç”¨åˆ°,åˆ™mescrollInitå¯ä»¥ä¸ç”¨é…ç½®
        },
        // èŽ·å–æŽ’è¡Œæ¦œå¥–åˆ—è¡¨
        get_list(firstRow, fetchNum) {
            this.$Api({
                api_name: 'kkl.user.rewardList',
                firstRow: firstRow,
                fetchNum: fetchNum
            }, (err, data) => {
                if (!err) {
                    let arr = data.data.rank_list_list
			        // å¦‚æžœæ˜¯ç¬¬ä¸€é¡µéœ€æ‰‹åŠ¨åˆ¶ç©ºåˆ—è¡¨
			        if (firstRow === 1) this.relief_list = []
			        // æŠŠè¯·æ±‚åˆ°çš„æ•°æ®æ·»åŠ åˆ°åˆ—è¡¨
			        this.relief_list = this.relief_list.concat(arr)
                    this.$nextTick(() => {
                        this.mescroll.endSuccess(arr.length)
                        this.mescroll.endBySize(arr.length, data.data.total)
                    })
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        // ä¸Šæ‹‰åŠ è½½
        upCallback(page, mescroll) {
            this.get_list((page.num-1)*page.size, page.size)
        },
        // é¢†å–å¥–åŠ±
        receive() {
            if (this.is_receive == 1) {
                return
            }
            this.$Api({
                api_name: 'kkl.user.getReward',
            }, (err, data) => {
                if (!err) {
                    this.$msg(data.data, 'success', 'middle', 1500)
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        }
    }
}



