import MescrollVue from 'mescroll.js/mescroll.vue'
import empty from "@/components/common/Empty.vue"
export default {
    components:{
        MescrollVue, empty
    },
    name: 'news_notice',
    data () {
        return {
            list: [],
            mescroll: null, // mescrollå®žä¾‹å¯¹è±¡
            mescrollDown:{
                use: false
            }, 
            mescrollUp: { // ä¸Šæ‹‰åŠ è½½çš„é…ç½®.
                callback: this.upCallback, // ä¸Šæ‹‰å›žè°ƒ,
                page: {
                    num: 0, 
                    size: 10 
                },
                htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
			},
        }
    },
    created() {
        
    },
    methods: {
        mescrollInit (mescroll) {
            this.mescroll = mescroll  // å¦‚æžœthis.mescrollå¯¹è±¡æ²¡æœ‰ä½¿ç”¨åˆ°,åˆ™mescrollInitå¯ä»¥ä¸ç”¨é…ç½®
        },
        get_news_list(firstRow, fetchNum) {
            this.$Api({
                api_name: 'kkl.index.noticeList',
                firstRow: firstRow,
                fetchNum: fetchNum
            }, (err, data) => {
                if (!err) {
                    let arr = data.data.notice_list
			        // å¦‚æžœæ˜¯ç¬¬ä¸€é¡µéœ€æ‰‹åŠ¨åˆ¶ç©ºåˆ—è¡¨
			        if (firstRow === 1) this.list = []
			        // æŠŠè¯·æ±‚åˆ°çš„æ•°æ®æ·»åŠ åˆ°åˆ—è¡¨
			        this.list = this.list.concat(arr)
                    this.$nextTick(() => {
                        this.mescroll.endSuccess(arr.length)
                        this.mescroll.endBySize(arr.length, data.data.total)
                    })
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        // ä¸Šæ‹‰åŠ è½½
        upCallback(page, mescroll) {
            this.get_news_list((page.num-1)*page.size, page.size)
        },
        view_detail(id) {
            this.$router.push({
                path: '/newsText',
                query: {
                    notice_id: id,
                    type: 1
                }
            })
        }
    }
}



