import MescrollVue from 'mescroll.js/mescroll.vue'
export default {
    components:{
        MescrollVue
    },
    name: 'receiveRecord',
    data () {
        return {
            list: [{
                title: 'æ€»é¢',
            },{
                title: 'æ•°é‡',
            },{
                title: 'å·²é¢†å–',
            },{
                title: 'ç”Ÿæˆæ—¶é—´',
            },{
                title: 'æ“ä½œ',
            }],
            relief_list: [],
            mescroll: null, // mescrollå®žä¾‹å¯¹è±¡
            mescrollDown:{
                use: false
            }, 
            mescrollUp: { // ä¸Šæ‹‰åŠ è½½çš„é…ç½®.
                callback: this.upCallback, // ä¸Šæ‹‰å›žè°ƒ,
