import { formCheck} from '@/config/checkout'
export default {
    components:{
        
    },
    name: 'exchangeGoods',
    data () {
        return {
            bill_list: [{
                num: '',
                content: '7å¤©å†…æµæ°´'
            },{
                num: '',
                content: '7å¤©å†…å·²æ'
            },{
                num: '',
                content: '7å¤©å†…å……å€¼'
            }],
            list: [{
                title: 'æ‰‹æœºçŸ­ä¿¡'
            },
            // {
            //     title: 'å¾®ä¿¡éªŒè¯'
            // },
            ],
            currentIndex: 0, // ç´¢å¼•
            cash: '', // çŽ°é‡‘
            money: '', // å…‘æ¢ä¹è±†
            count: '', // å…‘æ¢æ•°é‡
            safe_pwd: '', // å®‰å…¨å¯†ç 
            verify_code: '', //çŸ­ä¿¡éªŒè¯ç 
            sendAuthCode: false,
            auth_time: 0,
            mobile: ''
        }
    },
    created() {
        this.$Api({
            api_name: 'kkl.index.giftCardInfo',
            gift_card_id: this.$route.query.id
        }, (err, data) => {
            if (!err) {
                let res = data.data.gift_card_info
                this.cash = res.cash
                this.bill_list[0].num = res.flow
                this.bill_list[1].num = res.deposit
                this.bill_list[2].num = res.recharge
                this.money = res.money
            } else {
                this.$msg(err.error_msg, 'cancel', 'middle', 1500)
            }
        })
        this.get_user_info()
    },
    methods: {
        // èŽ·å–é“¶è¡Œä½™é¢
        get_user_info() {
            this.$Api({
                api_name: 'kkl.user.getUserInfo',
            }, (erra, res) => {
                if (!erra) {
                    this.mobile = res.data.mobile
                } else {
                    this.$msg(erra.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        // å‘é€éªŒè¯ç 
        get_verify() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: 'è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.sendVerifyCode',
                    mobile: this.mobile
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.sendAuthCode = true
                        this.auth_time = 60
                        var auth_timetimer =  setInterval(()=>{
                            this.auth_time--;
                            if(this.auth_time<=0){
                                this.sendAuthCode = false
                                clearInterval(auth_timetimer);
                            }
                        }, 1000);
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
        change_index(index) {
            this.currentIndex = index
        },
        // å…‘æ¢å¡åˆ¸
        exchange_now() {
            let checkItem = [{
                reg: 'noData',
                val: this.count,
                errMsg: 'å…‘æ¢æ•°é‡è¿˜ä¸èƒ½ä¸ºç©º'
            }, {
                reg: 'noData',
                val: this.safe_pwd,
                errMsg: 'å®‰å…¨å¯†ç ä¸èƒ½ä¸ºç©º'
            }, {
                reg: 'noData',
                val: this.verify_code,
                errMsg: 'éªŒè¯ç ä¸èƒ½ä¸ºç©º'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.index.exChangeCard',
                    safe_password: this.$MD5.hex_md5(this.safe_pwd),
                    number: this.count,
                    verify_code: this.verify_code,
                    gift_card_id: this.$route.query.id
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success', 'middle', 1500)
                        this.safe_pwd = ''
                        this.count = ''
                        this.verify_code = ''
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
    }
}



