import { formCheck} from '@/config/checkout'
export default {
    components:{
        
    },
    name: 'retrievePwd',
    data () {
        return {
            list: [{
                title: 'æ‰‹æœºçŸ­ä¿¡'
            },
            // {
            //     title: 'å¾®ä¿¡éªŒè¯'
            // },
            ],
            currentIndex: 0,
            type: 0,
            placeholder: '',
            img_code: '', // å›¾å½¢éªŒè¯ç è·¯å¾„
            mobile: '', // æ‰‹æœºå·
            code: '',  // å›¾å½¢éªŒè¯ç 
            verify_code: '', // çŸ­ä¿¡éªŒè¯ç 
            new_pwd: '', //æ–°å¯†ç 
            sendAuthCode: false,
            auth_time: 0,
        }
    },
    created() {
        this.get_img_code()
        this.type = this.$route.query.type
        if (this.type == 1) {
            this.placeholder = 'è¯·è¾“å…¥æ–°çš„å¯†ç '
        } else if(this.type == 3) {
            this.placeholder = 'è¯·è¾“å…¥æ–°çš„é“¶è¡Œå¯†ç '
        } else {
            this.placeholder = 'è¯·è¾“å…¥æ–°çš„å®‰å…¨å¯†ç '
        }
    },
    methods: {
        // å‘é€éªŒè¯ç 
        get_verify() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: 'è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.sendVerifyCode',
                    mobile: this.mobile
                }, (err, data) => {
                    if (!err) {
                        this.$msg(data.data, 'success','middle', 1500)
                        this.sendAuthCode = true
                        this.auth_time = 60
                        var auth_timetimer =  setInterval(()=>{
                            this.auth_time--;
                            if(this.auth_time<=0){
                                this.sendAuthCode = false
                                clearInterval(auth_timetimer);
                            }
                        }, 1000);
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        },
        // èŽ·å–å›¾å½¢éªŒè¯ç 
        get_img_code() {
            this.$Api({
                api_name: 'kkl.user.getImgCode',
            }, (err, data) => {
                if (!err) {
                    this.img_code = data.data.url
                } else {
                    this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                }
            })
        },
        // æ›´æ¢å›¾å½¢éªŒè¯ç 
        change_code_img() {
            this.img_code = ''
            this.get_img_code()
        },
        // é€‰æ‹©éªŒè¯æ–¹å¼
        change_index(index) {
            this.currentIndex = index
        },
        // æäº¤
        submit() {
            let checkItem = [{
                reg: 'mobile',
                val: this.mobile,
                errMsg: 'å¡«å†™æ­£ç¡®æ‰‹æœºå·'
            }, {
                reg: 'noData',
                val: this.code,
                errMsg: 'å¡«å†™å›¾å½¢éªŒè¯ç '
            }, {
                reg: 'noData',
                val: this.verify_code,
                errMsg: 'éªŒè¯ç ä¸èƒ½ä¸ºç©º'
            }, {
                reg: 'noData',
                val: this.new_pwd,
                errMsg: 'æ–°å¯†ç ä¸èƒ½ä¸ºç©º'
            }]
            if (formCheck(checkItem).result) {
                this.$Api({
                    api_name: 'kkl.user.findPassword',
                    mobile: this.mobile,
                    verify_code: this.verify_code,
                    code: this.code
                }, (err, data) => {
                    if (!err) {
                        this.$Api({
                            api_name: 'kkl.user.resetPassword',
                            mobile: this.mobile,
                            new_password: this.$MD5.hex_md5(this.new_pwd),
                            re_password: this.$MD5.hex_md5(this.new_pwd),
                            type: this.type
                        }, (err, data) => {
                            if (!err) {
                                this.$msg(data.data, 'success', 'middle', 1500)
                                setTimeout(() => {
                                    if (this.type == 1) {
                                        this.$native.back()
                                        // this.$native.native_dismiss()
                                    } else {
                                        this.$router.go(-1)
                                    }
                                },1500)
                            } else {
                                this.$msg(err.error_msg, 'cancel','middle', 1500)
                            }
                        })
                    } else {
                        this.$msg(err.error_msg, 'cancel', 'middle', 1500)
                    }
                })
            } else {
                this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
            }
        }
    }
}



