import {
  ALERT_TIME
} from '@/config/config'
import {
  formCheck
} from '@/config/checkout'
export default {
  name: "login",
  data () {
    return {
      mobile: '', // æ‰‹æœºå·
      pwd: '', // å¯†ç 
      realname: '', // æ˜µç§°
      referee: '', // æŽ¨èäºº
      verify_code: '', // çŸ­ä¿¡éªŒè¯ç 
      code: '', // å›¾å½¢éªŒè¯ç 
      sendAuthCode: false,
      auth_time: 0,
      img_code: '', // å›¾å½¢éªŒè¯ç 
    };
  },
  created () {
    this.get_img_code()
  },
  methods: {
    // èŽ·å–å›¾å½¢éªŒè¯ç 
    get_img_code () {
      this.$Api({
        api_name: 'kkl.user.getImgCode',
      }, (err, data) => {
        if (!err) {
          this.img_code = data.data.url
        } else {
          this.$msg(err.error_msg, 'error', 1500)
        }
      })
    },
    // æ›´æ¢å›¾å½¢éªŒè¯ç 
    change_code_img () {
      this.img_code = ''
      this.get_img_code()
    },
    // å‘é€éªŒè¯ç 
    get_verify () {
      let checkMobile = [{
        reg: 'mobile',
        val: this.mobile,
        errMsg: 'è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·'
      }, {
        reg: 'noData',
        val: this.code,
        errMsg: 'æ ¡éªŒç ä¸èƒ½ä¸ºç©º'
      }]
      if (formCheck(checkMobile).result) {
        this.$Api({
          api_name: 'kkl.user.sendVerifyCode',
          mobile: this.mobile
        }, (err, data) => {
          if (!err) {
            console.log(data)
            this.$msg(data.data, 'success', 'middle', 1500)
            this.sendAuthCode = true
            this.auth_time = 60
            var auth_timetimer = setInterval(() => {
              this.auth_time--;
              if (this.auth_time <= 0) {
                this.sendAuthCode = false
                clearInterval(auth_timetimer);
              }
            }, 1000);
          } else {
            this.img_code = ''
            this.get_img_code()
            this.$msg(err.error_msg, 'cancel', 'middle', 1500)
          }
        })
      } else {
        this.$msg(formCheck(checkMobile), 'cancel', 'middle', 1500)
      }
    },
    // æ³¨å†Œ
    register () {
      let checkItem = [{
        reg: 'mobile',
        val: this.mobile,
        errMsg: 'è¯·è¾“å…¥æ­£ç¡®çš„æ‰‹æœºå·'
      }, {
        reg: 'pwd',
        val: this.pwd,
        errMsg: 'å¯†ç è¯·è¾“å…¥6-20ä½å­—æ¯å’Œæ•°å­—'
      }, {
        reg: 'noData',
        val: this.realname,
        errMsg: 'æ˜µç§°ä¸èƒ½ä¸ºç©º'
      }, {
        reg: 'noData',
        val: this.verify_code,
        errMsg: 'çŸ­ä¿¡éªŒè¯ç ä¸èƒ½ä¸ºç©º'
      }, {
        reg: 'noData',
        val: this.code,
        errMsg: 'å›¾å½¢éªŒè¯ç ä¸èƒ½ä¸ºç©º'
      }]
      if (formCheck(checkItem).result) {
        this.$Api({
          api_name: 'kkl.user.signup',
          mobile: this.mobile,
          verify_code: this.verify_code,
          code: this.code,
          password: this.$MD5.hex_md5(this.pwd),
          again_password: this.$MD5.hex_md5(this.pwd),
          parent_id: this.referee,
          nickname: this.realname
        }, (err, data) => {
          console.log(data, err)
          if (!err) {
            this.$msg('æ³¨å†ŒæˆåŠŸ', 'success', 'middle', 1500)
            setTimeout(() => {
              this.$router.replace({
                path: '/login'
              })
            }, 1500);
            // this.$message({
            //   message: 'æ³¨å†ŒæˆåŠŸ',
            //   type: 'success',
            //   duration: ALERT_TIME,
            //   onClose: () => {
            //     this.$router.replace({
            //       path: '/login'
            //     })
            //   }
            // });
          } else {
            if (data.code === 40051) {
              this.img_code = ''
              this.get_img_code()
            }
            this.$msg(err.error_msg, 'cancel', 'middle', 1500)
          }
        })
      } else {
        this.$msg(formCheck(checkItem), 'cancel', 'middle', 1500)
      }
    },
    getPwd () {
      this.$router.push({
        path: '/login'
      })
    },
  }
}



