let filters = {
    formatDate: time => {
      return time ?
        (time * 1000 / (1000 * 60 * 60 * 24 * 365) == new Date().getFullYear()) ?
        new Date(time * 1000).format('yyyy-MM-dd hh:mm') :
        new Date(time * 1000).format('MM-dd hh:mm') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateYear: time => {
      return time ? new Date(time * 1000).format('yyyy/MM/dd') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateYearStyle: time => {
      return time ? new Date(time * 1000).format('yyyy-MM-dd') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateYearTime: time => {
      return time ? new Date(time * 1000).format('yyyy-MM-dd hh:mm:ss') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateYearTimeMin: time => {
      return time ? new Date(time * 1000).format('yyyy-MM-dd hh:mm') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateYearDot: time => {
      return time ? new Date(time * 1000).format('yyyy.MM.dd') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateMonth: time => {
      return time ? new Date(time * 1000).format('MM-dd hh:mm:ss') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateMonthDot: time => {
      return time ? new Date(time * 1000).format('MM.dd') : 'æœªçŸ¥æ—¶é—´'
    },
    formatDateFont: time => {
      return time ? new Date(time * 1000).format('yyyyå¹´MMæœˆddæ—¥ hh:mm:ss') : 'æœªçŸ¥æ—¶é—´'
    },
    formatTime: time => {
      return time ? new Date(time * 1000).format('MMæœˆddæ—¥ hh:mm') : 'æœªçŸ¥æ—¶é—´'
    },
    formatTimeDay: time => {
      return time ? new Date(time * 1000).format('hh:mm:ss') : 'æœªçŸ¥æ—¶é—´'
    },
    // å°†æ•°æ®ç”¨é€—å·åˆ†éš”    999999999 ->   999,999,999  
    changeBigNum: num => {
      if(!num){
        return 0
      }
      var str = parseInt(num).toString();
      var reg = str.indexOf(".") > -1 ? /(\d)(?=(\d{3})+\.)/g : /(\d)(?=(?:\d{3})+$)/g;
      return str.replace(reg,"$1,"); 
    }
  }
  
  Date.prototype.format = function (fmt) {
    var o = {
      "M+": this.getMonth() + 1, //æœˆä»½   
      "d+": this.getDate(), //æ—¥   
      "h+": this.getHours(), //å°æ—¶   
      "m+": this.getMinutes(), //åˆ†   
      "s+": this.getSeconds(), //ç§’   
      "q+": Math.floor((this.getMonth() + 3) / 3), //å­£åº¦   
      "S": this.getMilliseconds() //æ¯«ç§’   
    };
    if (/(y+)/.test(fmt))
      fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
      if (new RegExp("(" + k + ")").test(fmt))
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  };
  export default filters


