export function getTableType(table_type) {
  var gameTableType;
  switch (parseInt(table_type)) {
    case 1:
      gameTableType = 6
      break;
    case 2:
      gameTableType = 6
      break;
    case 3:
      gameTableType = 3
      break;
    case 4:
      gameTableType = 3
      break;
    case 5:
      gameTableType = 6
      break;
    case 6:
      gameTableType = 6
      break;
    case 7:
      gameTableType = 3
      break;
    case 8:
      gameTableType = 6
      break;
    case 9:
      gameTableType = 3
      break;
    case 10:
      gameTableType = 6
      break;
    case 11:
        gameTableType = 6
        break;
    default:
      gameTableType = 0
  }
  return gameTableType
}
